import React from "react";
import cl from "./DayRadio.module.css"
import data from "../../../../Time.json"
import { Title3, Footnote } from "../../../../textStyles/TextStyleComponents";
import { useState, useEffect } from "react";

const DayRadio = ({ calendarData, scrollSetMonthName, handleSetSelectedDate }) => {
    const [selectedItem, setSelectedItem] = useState(0);
    const [scrollPixels, setScrollPixels] = useState(0);
    const [visibleItems, setVisibleItems] = useState([]);


    useEffect(() => {
        //       
        const updatedVisibleItems = calendarData.map(item => {
            const formattedDate = `${item.day}.${item.month + 1}.${item.year}`;
            const slots = data.days[formattedDate] || [];
            return slots.some(slot => slot.status === "free");
        });
        setVisibleItems(updatedVisibleItems);
    }, [calendarData]);

    const handleItemClick = (index) => {
        setSelectedItem(index);
        handleSetSelectedDate(index);
    };

    const handleScroll = (e) => {
        const scrolled = e.target.scrollLeft;
        setScrollPixels(scrolled);
        scrollSetMonthName(scrolled);


    }



    return (
        <div className={cl.forScrollbarNone}>
            <div className={cl.scrollContainer} onScroll={handleScroll}>
                {calendarData.map((item, index) => (
                    <label key={index} htmlFor={`day${index}`}>
                        <div className={cl.scrollDays}>
                            <div className={cl.scrollDay}>
                                <input
                                    type="radio"
                                    id={`day${index}`}
                                    name="days"
                                    className={cl.radio}
                                    checked={selectedItem === index}
                                    onChange={() => handleItemClick(index)}


                                />

                                {/* <Title3 style={{ color: !visibleItems[index] ? 'red' : (selectedItem === index ? 'blue' : 'inherit') }}>
                                    {item.day}
                                </Title3> */}
                                {/* <Title3 style={{ color: selectedItem === index ? 'blue' : (!visibleItems[index] ? 'red' : 'inherit') }}>
                                    {item.day}
                                </Title3> */}

                                <Title3 style={{ opacity: !visibleItems[index] ? 0.5 : 1, color: selectedItem === index ? 'blue' : 'inherit' }}>
                                    {item.day}
                                </Title3>


                                <Footnote>{item.shortDayOfWeek}</Footnote>

                            </div>
                        </div>
                    </label>
                ))}
            </div>
        </div>
    )
}

export default DayRadio;