import React, { useEffect } from "react";
import cl from "./Calendar.module.css";
import { useState } from "react";
import { HeadLineSemibold, HeadLineMonoNumbers } from "../../../textStyles/TextStyleComponents";
import DayRadio from "./dayRadio/DayRadio";
import FreeTime from "../freeTime/FreeTime";


const Calendar = () => {

  const currentDate = new Date();
  const currentMonth = currentDate.getMonth();
  const currentDay = currentDate.getDate();
  const currentYear = currentDate.getFullYear();
  const currentWeekDay = currentDate.getDay();

  const monthNames = ["", "", "", "", "", "", "", "", "", "", "", ""];
  // const daysOfWeek = ["", "", "", "", "", "", ""];

  const [monthName, setMonthName] = useState(monthNames[currentMonth]);
  const [selectedDate, setSelectedDate] = useState({
    day: currentDay,
    month: currentMonth,
    year: currentYear
  });

  

  const scrollSetMonthName = (scrolled) => {
    if (scrolled > 1450) {
      setMonthName(monthNames[currentMonth + 1]);
    } else if (scrolled < 1450) {
      setMonthName(monthNames[currentMonth])
    }
  }

  const getDaysInMonth = (year, month) => {
    return new Date(year, month + 1, 0).getDate();
  };

  const getShortDayOfWeek = (year, month, day) => {
    const date = new Date(year, month, day);
    return new Intl.DateTimeFormat('ru-RU', { weekday: 'short' }).format(date);
  };



  const generateCalendarData = (startYear, startMonth, startDay, count) => {
    const calendarData = [];
    let year = startYear;
    let month = startMonth;
    let day = startDay;
    let daysGenerated = 0;

    while (daysGenerated < count) {
      const daysInMonth = getDaysInMonth(year, month);
      for (; day <= daysInMonth && daysGenerated < count; day++) {
        const shortDayOfWeek = getShortDayOfWeek(year, month, day);
        calendarData.push({ day, shortDayOfWeek, month, year });
        daysGenerated++;
      }
      day = 1;
      month++;
      if (month === 12) {
        month = 0;
        year++;
      }
    }

    return calendarData;
  };

  const calendarData = generateCalendarData(currentYear, currentMonth, currentDay, 60);

  const handleSetSelectedDate = (index) => {
    const selectedDateInfo = calendarData[index];
    setSelectedDate({
      day: selectedDateInfo.day,
      month: selectedDateInfo.month,
      year: selectedDateInfo.year
  })
  
  }

  

  return (
    <div className={cl.container}>

      <div className={cl.monthYearContainer}>
        <HeadLineSemibold className={cl.monthText}>{monthName}</HeadLineSemibold>
        <HeadLineMonoNumbers className={cl.yearText}>{currentYear}</HeadLineMonoNumbers>
      </div>
      <DayRadio calendarData={calendarData} scrollSetMonthName={scrollSetMonthName} handleSetSelectedDate={handleSetSelectedDate}/>

      <FreeTime selectedDate={selectedDate}/>

    </div>
  )
}

export default Calendar;